
<template lang="jade">

.box-container
  .box(v-if="type=='normal'||type=='disabled'", :class="type")
  .box(v-if="type=='specia'", class="specia", :class="{'rotate': rotate}", @click="clickBox")
    .front(:style="background")
      div.content
        span {{index}}
      p.tag {{title}} 
    .back(:class="'back'+back")

</template>
<style lang="stylus" scoped>
@import '../styles/main.styl'

*
  box-sizing border-box
.box-container
  width box-size 
  height @width 
  cursor pointer
  .rotate 
    transform rotateY(180deg)
  .box 
    width 100%
    height 100%
    position relative 
    transition 0.6s 
    transform-style preserve-3d
    .front, .back 
      position absolute 
      backface-visibility hidden
      width 100%
      height 100%
      border-radius 5%
    .front 
      background transparent
      display flex
      flex-flow column nowrap
      justify-content space-between
      &:hover 
        p.tag, .content 
          opacity 1
      p.tag
        transition opacity .3s linear
        display block
        font-size 1.2rem
        font-family 'Abel', sans-serif
        color rgba(233, 233, 233, 1)
        opacity 0
        padding 0.5rem 0 0.5rem 0.6rem
      .content 
        transition opacity .3s linear
        width 100%
        padding 0.4rem
        padding-top 0.2rem
        display block
        opacity 0
        span
          font-size 3rem 
    .back 
      transform rotateY(180deg)
    .back1
      background url(../assets/akalin/1.png)
      background-size cover
    .back2
      background url(../assets/akalin/2.png)
      background-size cover
    .back3
      background url(../assets/akalin/3.png)
      background-size cover
    .back4
      background url(../assets/akalin/4.png)
      background-size cover

// 给普通块添加背景变化
.normal, .disabled
  background transparent
  &:hover 
    background #191919
    transition background 0s linear

</style>
<script>

export default {
  data () {
    return {
      isLeave: false,
      rotate: false,
      back: 1,
      color: ['#35A8C3', 'rosybrown', 'deepskyblue', 'chocolate', 'teal', 'brown', 'forestgreen', 'lightpink', 'slategray', 'gray']
    }
  },
  created (){
    this.back = Math.ceil(Math.random() * 3 + 1)
  },
  computed: {
    background () {
      if (this.type === 'specia') {
        return 'background:' + _.sample(this.color)
      } else {
        return ''
      }
    }
  },
  methods: {
    mouseOut () {
    }, 
    clickBox () {
      if (this.rotate) {
        return;
      }
      this.rotate = true
      setTimeout(() => {
        this.rotate = false
        if (this.href) {
          return this.$router.push({ path: this.href })
        }
      }, 800)
    }
  },
  props: {
    type: {
      type: String,
      default: 'normal'
    },
    bg: {
      type: String,
      default: ''
    },
    href: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: 'links'
    },
    index: {
      type: Number,
      default: null
    }
  }

}
  
</script>


