
<template lang="pug">

.back-to-top(:class="{'show': show}", @click="back", ref="back-to-top")
  icon(name="chevron-up").icon

</template>

<script>

export default {
  data () {
    return {
      show: false
    }
  },
  props: {
    speed: {
      type: Number,
      default: 0.6
    }

  },
  methods: {
    back () {

      let top = document.body.scrollTop 
      let timer = setInterval(() => {
        top -= Math.abs(top * this.speed)
        if (top <= 1) {
          top = 0
          clearInterval(timer)
        }
        document.body.scrollTop = top
      }, 30)
      return false

    },
    scroll (e) {

      if (document.body.scrollTop > 200) {
        this.show = true
      } else {
        this.show = false
      }
      
    }
  },
  mounted () {

    document.addEventListener('scroll', this.scroll)
    
  }
}
  
</script>

<style lang="stylus" scoped >

.back-to-top 
  position fixed
  right 80px
  bottom 50px
  background #fff
  width 40px
  height 40px
  border-radius 50%
  cursor pointer
  transition all .2s ease-in-out
  box-shadow 0px 2px 4px 1px rgba(0, 0, 0, 0.25)
  z-index 1
  display block
  visibility hidden
  font-size 10px
  &:hove2
    color green
  >.icon
    position absolute 
    left 50%
    top 50%
    transform translate(-50%, -60%)
  &.show 
    font-size 20px
    visibility visible

  
</style>


