
<template lang="jade">

.lodash(:key="tag")
  .content(v-html="content")

</template>
<script>

import hljs from 'highlight.js'
import 'highlight.js/styles/hybrid.css'
import _ from 'lodash/fp'

export default {
  computed: {
    tag () {
      return this.$route.params.tag
    },
    content () {
      return this.tag && require(`./${this.tag}.md`)
    }
  },
  watch: {
    // hack 响应lodash/index组件之间跳转
    tag (val) {
      this.content = val && require(`./${val}.md`)
    },
    // 内容改变时重新渲染code块
    content (val) {
      this.$nextTick(() => {
        let codes = document && document.getElementsByTagName('code')

        _.forEach((val) => {
          hljs.highlightBlock(val)
        })(codes)
      })
    }
  },
  data () {
    return {
      content: ''
    }
  },
  created () {
    this.content = require(`./${this.tag}.md`)
    hljs.configure({
      tabReplace: '  '
    })
  }

}
  
</script>
<style lang="stylus" >

.lodash 
  width 100%
  min-height 30rem
  background #EEEEEE
  box-sizing border-box
  overflow hidden
  margin 0 auto
  padding 2rem
  .content
    h1
      margin 1rem 0 0.8rem 0
      font-size 3rem
    h2 
      margin 0.8rem 0 0.4rem 0
    code 
      margin .5rem 0
  
</style>


