
import * as types from '../types'

const state = {
  showLoading: false    
}
  
const getters = {
    [types.getLoadingType] (state) {
      return state.showLoading
    }
}
 
const mutations = {
    [types.hideLoading] (state) {
      state.showLoading = false
    },
    [types.showLoading] (state) {
      state.showLoading = true
    }
}

export default {
  state,
  getters,
  mutations
}
  


